@use '../../../../../../apps/sanare-app/src/styles/base/mixins' as *;

.upload-image {
  position: relative;
  margin: 0 auto 25px auto;
  // margin-bottom: 16px;
  // margin-bottom: 25px;
  border-radius: 50%;

  &:hover .remove-wrapper {
    opacity: 1;
  }

  width: 130px;
  height: 130px;
  // overflow: hidden;
  background-image: url('../../../../../../apps/sanare-app/src/assets/images/user.svg');
  background-size: cover;
  background-position: center;

  input[type='file'] {
    display: none;
  }

  .file-upload-button-wrapper {
    position: absolute;
    top: 93px;
    right: -15px;
    padding: 3px;
    border-radius: 50%;
    // background-color: #faf9fd;
    z-index: 9999;

    .file-upload-button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      // border: 5px solid #faf9fd;
      // background: #cccccc;
      // transition: background 0.1s ease-in;

      &:hover {
        cursor: pointer;
        // background: hsl(0, 0%, 87%);
      }

      &:active {
        transform: scale(0.95);
      }
    }
  }

  .remove-wrapper {
    top: 7px;
    right: -5px;
    opacity: 0;
    transition: all 0.1s ease-in;

    .file-upload-button {
      width: 26px;
      height: 26px;
      // background: #ba1a1a;
      // transition: all 0.1s ease-in;
      // color: white;

      &:hover {
        cursor: pointer;
        // background: hsl(0, 75%, 47%);
      }

      &:active {
        transform: scale(0.95);
      }
    }

    /* .remove-button {
      z-index: 99999;
      background-color: #cccccc;
    } */
  }

  .profile-img-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    width: 130px;
    height: 130px;
    overflow: hidden;

    img {
      width: 100%;
    }
  }
  // .upload-button {
  //   position: absolute;
  //   top: 80px;
  //   right: -12px;
  //   background: rgb(255, 255, 255);
  //   z-index: 9999;
  // }

  @include respond(compact) {
    margin-top: 16px;
    margin-bottom: 40px;
  }
}
